body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans", sans-serif;
}

.App {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "navbar"
    "router";
  height: 100vh;
  min-width: 320px;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.Router {
  grid-area: router;
  height: 1fr;
  overflow-y: auto;
  background-color: lightgray;
}

.Auth {
  padding: 1em;
  text-align: center;
}

.Auth form {
  margin-top: 1em;
}

.Navbar {
  grid-area: navbar;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-areas: "title dropdown user";
  padding: 0;
  margin: 0;
  background-color: cornflowerblue;
}

.Navbar .Title {
  text-align: left;
  background-color: antiquewhite;
  font-family: "Merriweather", serif;
  -webkit-align-self: center;
          align-self: center;
  justify-self: left;
  margin: 0.5em;
  padding: 0.25em;
  border-radius: 0.125em;
}

.Navbar .Dropdown {
  -webkit-align-self: center;
          align-self: center;
  margin: 0.5em;
  z-index: 2;
  background-color: royalblue;
  text-align: left;
  width: auto;
  max-width: 10em;
  border-radius: 0.125em;
}
.Navbar .Dropdown ul {
  background-color: cornflowerblue;
}

.Navbar .Title a:link,
.Navbar .Title a:visited {
  color: black;
  text-decoration: none;
}

h2 {
  padding: 0;
  margin: 0;
  margin-left: 0em;
  margin-right: 0;
  justify-self: left;
  font-weight: lighter;
}

.UserIcon .userName {
  justify-self: center;
}

.UserIcon a:link,
.UserIcon a:visited {
  text-decoration: none;
}

.UserIcon {
  grid-area: user;
  display: grid;
  grid-template-columns: 1fr 2em;
  margin: 0.5em;
  justify-self: right;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.UserIcon img {
  height: 2em;
  width: 2em;
  border-radius: 50%;
}

.UserIcon .userName {
  color: white;
  padding-right: 0.5em;
}

.UserIcon a {
  justify-self: center;
  display: -webkit-flex;
  display: flex;
}

.Dropdown {
  cursor: pointer;
  background-color: yellow;
  z-index: 1;
  text-align: center;
  height: 2em;
}

.Dropdown p {
  margin: 0.25em;
}

.Dropdown-List-Inactive {
  padding: 0px;
  margin: 0px;
  height: 0px;
  background-color: white;
  visibility: hidden;
}

.Dropdown-List-Active {
  position: relative;
  margin: 0px;
  background-color: #ffff66;
}

.Dropdown-Li {
  padding: 5px;
}

.Dropdown-Li:hover {
  background-color: white;
  color: black;
}

.Article {
  background-color: white;

  display: grid;
  margin: 1px;
  padding: 0.5em;
  grid-template-rows: auto 2em 1fr;
  grid-template-columns: 2em 1fr 4em;
  grid-template-areas:
    "title title title"
    "avatar author votes"
    "body body body";
}

li {
  list-style-type: none;
  margin-bottom: 4px;
  margin-top: 4px;
}

.Article > * {
  margin-top: 0.25em;
  margin-bottom: 0.25em;
}

.Article .avatar {
  grid-area: avatar;
  margin: 0;
}

.Article .avatar img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.Article .title {
  font-family: "Merriweather", serif;
  text-align: left;
  grid-area: title;
  margin-top: 0.25em;
  margin-bottom: 0.25em;
  background-color: peachpuff;
}

.Article .title a:link,
.Article .title a:visited {
  color: black;
  text-decoration: none;
}

.Article .topic {
  grid-area: topic;
  margin-top: 0.125em;
  margin-left: 0.125em;
  font-size: 0.8em;
}

.Article .author {
  grid-area: author;
  color: darkslategray;
  margin: 0.5em;
  margin-left: 0.5em;
  font-size: 0.8em;
  align-self: center;
  background-color: paleturquoise;
}

.Article .Body {
  grid-area: body;
  background-color: peachpuff;
  font-family: "Merriweather", serif;
  font-weight: lighter;
  margin-top: 1em;
}

.Votes {
  grid-area: votes;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "down count up";
  background-color: palegreen;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  color: darkslategray;
}

.Votes .down {
  grid-area: down;
}

.Votes .count {
  grid-area: count;
}

.Votes .up {
  grid-area: up;
}

.Votes .Voted {
  color: orange;
}

